<template>
  <div class="findFace">
    <Stare/>
    <div class="user-icon">
      <video
        width="780"
        height="550"
        ref="videoDom"
        id="video"
        preload
        autoplay
        loop
        muted
      ></video>
      <canvas width="780" height="550" ref="canvasDOM"></canvas>
    </div>

    <h3 class="findFaceFOnt">{{ loding }}</h3>
    <div class="button" @click="initTracker"></div>
  </div>
</template>

<script>
import Stare from '../components/stare.vue';
require("tracking/build/tracking-min.js");
require("tracking/build/data/face-min.js");
// ,
// require('tracking/build/data/mouth-min.js')
// require('tracking/build/data/eye-min.js')
//let objects = new tracking.ObjectTracker(['face', 'eye', 'mouth']);
// require('tracking/examples/assets/stats.min.js')

export default {
  name: "testTracking",
  components:{
    Stare
  },  
  data() {
    return {
      // 
      access_token: "",
      count: 0,
      isdetected: "",
      loding: "",
      imgbase64: "",
    };
  },
  methods: {
    // racker
    initTracker() {
      // alert('')
      // alert(navigator.mediaDevices)
      // ,,
      // https
      navigator.mediaDevices
        .getUserMedia({ video: true, audio: true })
        .then(this.getMediaStreamSuccess)
        .catch(this.getMediaStreamError);

      this.context = this.canvas.getContext("2d");

      // tracking
      this.tracker = new tracking.ObjectTracker("face");
      this.tracker.setInitialScale(4);
      this.tracker.setStepSize(2);
      this.tracker.setEdgesDensity(0.1);
      this.tracker.on("track", (event) => {
        this.onTracked(event);
      });

      // tracking,
      // tracking.track(this.video, this.tracker, { camera: true });

      // trackerTask.stop trackerTask.run
      this.trackerTask = tracking.track(this.video, this.tracker);
    },
    // 
    onTracked(event) {
      // , stop
      if (this.count >= 21) {
        this.onStopTracking();
        return;
      }

      // 
      this.context.clearRect(0, 0, this.canvas.width, this.canvas.height);
      event.data.forEach((rect) => {
        this.context.lineWidth = 1;
        this.context.strokeStyle = "#a64ceb";
        //'#a64ceb';
        this.context.strokeRect(rect.x, rect.y, rect.width, rect.height);
        this.context.font = "11px Helvetica";
        this.context.fillStyle = "#fff";
        // 

        if (event.data.length > 0 && this.count <= 20) {
          if (this.count < 0) {
            this.count = 0;
          }
          this.count += 1;
          if (this.count > 20) {
            this.isdetected = "";
            this.getPhoto();
          }
        } else {
          this.count -= 1;
          if (this.count < 0) {
            this.isdetected = "";
          }
        }
      });
      // 
      this.context.fillText(this.isdetected, 100, 30);
    },
    // 
    onStopTracking() {
      this.trackerTask.stop();
      this.video.pause();
      // 
      this.video.srcObject = null;
      window.stream.getTracks().forEach((track) => track.stop());
    },
    // 
    getPhoto() {
      this.isdetected = "";
      this.context.clearRect(0, 0, this.canvas.width, this.canvas.height);
      let video = document.getElementById("video");
      this.context.drawImage(
        video,
        0,
        0,
        this.canvas.width,
        this.canvas.height
      );
      let dataUrl = this.canvas.toDataURL("image/jpeg", 1);
      this.imgbase64 = dataUrl.replace(/^data:image\/\w+;base64,/, "");

      // 
      this.postFace();
    },
    // Token
    getToken() {
      this.PostAxios("faceGetToken", {}).then((res) => {
        this.access_token = res.data.access_token;
        window.sessionStorage.setItem("access_token", this.access_token);
        console.log(res);
      });
    },
    // 
    postFace() {
      this.loding = "";
      this.PostAxios("faceFirm", {
          access_token: this.access_token,
          img: this.imgbase64,
        })
        .then((res) => {
          console.log(res);
          this.loding = "";
          if (res.data.error_code !== 0) {
            if (res.data.error_code == 223120) {
              this.$message.error("");
            }
          } else {
            if (res.data.result.user_list[0].score > 80) {
              this.$message.success("");
              this.$router.push("/home");
            } else {
              this.$message.error(",");
            }
          }
        });
    },
    // 
    getMediaStreamSuccess(stream) {
      window.stream = stream;
      this.video.srcObject = stream;
    },
    // 
    getMediaStreamError(error) {
      alert("" + error);
    },
  },
  mounted() {
    this.video = this.$refs.videoDom;
    this.canvas = this.$refs.canvasDOM;

    //tonken
    this.getToken();
  },
  destroyed() {},
};
</script>

<style scoped>
.findFace {
  width: 100vw;
  height: 100vh;
  background-color: #072778;
}
.user-icon {
  position: relative;
  top: 10vh;
  margin: 0 auto;
  width: 40vw;
  height:50vh;
}
.button {
  width: 20vw;
  height: 50px;
  line-height: 50px;
  margin: 30vh auto;
  background-color: skyblue;
  color: white;
  text-align: center;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  position: absolute;
  transform: translate(-50%);
  top: 50%;
  left: 50%;
}
video,
canvas {
  position: absolute;
}
.findFaceFOnt{
  font-size: 20px;
  font-weight: 600;
  color: aliceblue;
  z-index: 100;
}
</style>