<template>
  <div>
    <div id="particles-js">
      <canvas
        class="particles-js-canvas-el"
        width="1920"
        height="1080"
        style="width: 100%; height: 100%"
      ></canvas>
    </div>
  </div>
</template>

<script>
export default {
  mounted() {
    particlesJS("particles-js", {
      particles: {
        number: {
          value: 40, //
          density: {
            enable: true, //
            value_area: 800, //
          },
        },
        color: {
          value: "#f3eaa1", //
        },
        shape: {
          type: "star", // "circle" ,"edge" ,"triangle" ,"polygon" ,"star" ,"image" ,["circle", "triangle", "image"]
          stroke: {
            width: 0, //
            color: "#f3eaa1", //
          },
          polygon: {
            nb_sides: 5, // 
          },
          image: {
            src: "img/github.svg", //  "assets/img/yop.svg" , "http://mywebsite.com/assets/img/yop.png"
            width: 100, //
            height: 100, //
          },
        },
        opacity: {
          value: 1, //
          random: true, //
          anim: {
            enable: true, //
            speed: 1, // 
            opacity_min: 0, //
            sync: true,
          },
        },
        size: {
          value: 3, //
          random: true, // 
          anim: {
            enable: false, // 
            speed: 4, //
            size_min: 0.3,
            sync: false,
          },
        },
        line_linked: {
          enable: false, //
          distance: 150, //
          color: "#ffffff", //
          opacity: 0.4, //
          width: 1, //
        },
        move: {
          enable: true, //
          speed: 1, //
          direction: "none", //   "none" ,"top" ,"top-right" ,"right" ,"bottom-right" ,"bottom" ,"bottom-left" ,"left" ,"top-left"
          random: true, //
          straight: false, //
          out_mode: "out", //
          bounce: false, //
          attract: {
            enable: false, // 
            rotateX: 600, //X
            rotateY: 600, //Y
          },
        },
      },
      interactivity: {
        detect_on: "canvas", // "canvas", "window"
        events: {
          onhover: {
            enable: true, //
            mode: "bubble", //      "grab","bubble","repulse",["grab", "bubble"]
          },
          onclick: {
            enable: false, //
            mode: "repulse", //   "push" ,"remove" ,"bubble" ,"repulse" ,["push", "repulse"]
          },
          resize: true, // 
        },
        modes: {
          grab: {
            distance: 100, //
            line_linked: {
              opacity: 0.8, //
            },
          },
          bubble: {
            distance: 250, //
            size: 4, // 
            duration: 2, //
            opacity: 1, //
            speed: 3,
          },
          repulse: {
            distance: 400, //
            duration: 0.4, //
          },
          push: {
            particles_nb: 4, //
          },
          remove: {
            particles_nb: 2,
          },
        },
      },
      retina_detect: true,
    });
  },
};
</script>
<style lang="scss" scoped>
#particles-js {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
}
</style>